import React from "react";
import { inject } from "mobx-react";
import Modal from "../Modal/Modal";

import {
  ModalDiscountStyled,
  DiscountImage,
  Title,
  Description,
  ButtonBack,
  Content,
  Background
} from "./ModalDiscount.styled";

const ModalDiscount = ({ closeModalDiscount, isShowModalDiscount }) => {
  const handleClose = () => {
    closeModalDiscount();
  };

  return (
    <Modal isShow={isShowModalDiscount} onClose={handleClose}>
      <ModalDiscountStyled>
        <Background />
        <Content>
          <DiscountImage />
          <Title>  !</Title>
          <Description>
            !      ! 5%- 
                       .
          </Description>
          <ButtonBack onClick={handleClose}> !</ButtonBack>
        </Content>
      </ModalDiscountStyled>
    </Modal>
  );
};

export default inject(({ modalStore }) => ({
  closeModalDiscount: modalStore.closeModalDiscount,
  isShowModalDiscount: modalStore.isShowModalDiscount
}))(ModalDiscount);
